* {
  font-family: sans-serif;
}

ul {
  list-style: none;
}

.gallery {
  flex-wrap: wrap;
  gap: 30px;
  width: 1284px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0;
  display: flex;
}

img {
  object-fit: cover;
  opacity: 0;
  border: 4px solid #000;
  width: 400px;
  height: 200px;
  transition: all .5s;
  transform: scale(.95);
}

img.loaded {
  opacity: 1;
  transform: scale(1);
}

.gallery-button {
  cursor: pointer;
  background-color: #ff0;
  border: 2px solid #000;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 5px 10px;
  font-size: 20px;
  font-weight: 900;
  transition: all .5s ease-in-out;
  display: flex;
}

.gallery-button:hover {
  opacity: .7;
}
